.container-forms{
    display: flex;
    flex-grow:1;
  }
.container-cadastro{
    display: flex;
    flex: 1;
    height: 90vh;
    flex-direction: column
  }
.container-form-funcionario{
    display: flex;
    flex: 1;
    align-items: center;
    flex-direction: column
  }
.container-logo{
    display: flex;
    flex: 0.7;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }
@media (max-width:603px){
    .container-logo{
        margin-top:50px
    }
    .container-form-funcionario{
        margin-bottom: 20px;
        margin-top: 20px;
    }
    .container-cadastro{
        height: 200vh;
        flex-basis: auto;
        flex-direction: column;
   
    }
    .container-forms{
        flex-direction: column;
    }
}

