.button-menu{
    background-color: black;
    border  : none;
    margin      : 0px;
    padding     : 0.5rem;
    font-size   : 1.2rem;
    color: white;
    cursor: pointer;
    max-width: 150px;
}
.button-menu-selected{
    background-color: white; 
    color: black;
    border-width: 0;
    border-style:none;
    margin      : 0px;
    padding     : 0.5rem;
    font-size   : 1.2rem;
    max-width: 150px;
    cursor: pointer;
}
.menu{
    display: flex;
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: black;
    justify-content: space-around;
}
@media (max-width:790px) and (min-width:603px){
    .button-menu{
        background-color: black;
        border  : none;
        margin      : 0px;
        padding     : 0.5rem;
        font-size   : 1rem;
        color: white;
        cursor: pointer;
        max-width: 110px;
    }
    .button-menu-selected{
        background-color: white; 
        color: black;
        border-width: 0;
        border-style:none;
        margin      : 0px;
        padding     : 0.5rem;
        font-size   : 1rem;
        max-width: 110px;
        cursor: pointer;
    }

}

@media (max-width:603px){
    .menu{
        display: none;
    }
}
@media  (max-height:600px) {
    .menu{
        display: none;
    }
}
.button-menu:hover{
    background-color: white;
    color:black;
}
.link-menu{
    color:inherit;
    text-decoration: none;
}
.route-name{
    color:inherit;
    text-decoration: none;
}
a{
    color:inherit;
    text-decoration: none;
}